@import "text";

.plan-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  margin: 0px 20px;
  color: $textColor;

  .step {
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 23px;
    margin: 0px 0px 5px;
    text-align: center;
  }
  span {
    margin: 5px 0px 10px;
    text-transform: uppercase;
    font-size: 20px;
  }
  img {
    width: 200px;
    height: 110px;
    outline: 3px solid black;
    border: 6px solid white;
  }
  .plan-description {
    font-size: 16px;
    text-align: center;
    width: 220px;
    margin: 10px 0px;
  }
}

@media all and (min-width: 768px) {
  .plan-block {
    .step {
      font-size: 23px;
    }
    span {
      font-size: 20px;
    }
    img {
      width: 200px;
      height: 110px;
    }
    .plan-description {
      font-size: 16px;
      width: 220px;
    }
  }
}

@media all and (min-width: 600px) and (max-width: 768px) {
  .plan-block {
    margin: 0px 15px;
    .step {
      font-size: 18px;
    }
    span {
      font-size: 16px;
    }
    img {
      width: 140px;
      height: 75px;
    }
    .plan-description {
      font-size: 14px;
      width: 150px;
    }
  }
}

@media all and (min-width: 480px) and (max-width: 600px) {
  .plan-block {
    flex-direction: row;
    justify-content: space-around;
    margin: 0px 5px 10px;
    .step {
      font-size: 16px;
    }
    span {
      font-size: 16px;
    }
    img {
      width: 140px;
      height: 75px;
    }
    .plan-description {
      font-size: 14px;
      width: 210px;
      margin: 0px 30px 0px;
    }
    .column {
      img {
        align-self: flex-start;
      }
    }
  }
}
@media all and (max-width: 480px) {
  .plan-block {
    flex-direction: row;
    margin: 0px 5px 10px;
    .step {
      font-size: 16px;
    }
    span {
      font-size: 16px;
    }
    img {
      width: 140px;
      height: 75px;
    }
    .plan-description {
      font-size: 14px;
      width: 50%;
      margin: 0px 30px 0px;
    }


  }
}
